import React, { useState } from 'react';

import './Project.css';
import VideoPlayer from './VideoPlayer';


const Project = ({name, content, video, image, frontend, backend, demo, github, timestamps}) => {


    var chrome   = navigator.userAgent.indexOf('Chrome') > -1;
    var opera  = window.opera | window.opr | ( navigator.userAgent.indexOf(' OPR/') > -1 ) | ( navigator.userAgent.indexOf(' Coast/') > -1 ) | ( navigator.userAgent.indexOf(' OPiOS/') > -1 );

    const [isChrome, setIsChrome] = useState(chrome)
    const [isOpera, setIsOpera] = useState(opera)

    return (
        <div className="app">
            <div className="img-refs">
                <div className="image">
                    {isChrome && !isOpera ? ( 
                        <VideoPlayer video={video} timestamps={timestamps} />
                    ) : (
                        <img src={image} alt="name" />
                    )}
                </div>
                <div className="references">
                    <a href={demo} className="refs">demo</a>
                    <a href={github} className="refs">github</a>
                </div>
            </div>
            <div className="desc">
                <h3>{name}</h3>
                <p className="content">{content}</p>
                {frontend.length > 0 && (
                    <div className="frontend">
                        <h4>Frontend</h4>
                        <p>
                            {frontend.map((tech, index) => <small key={index}>{tech}</small>)}
                        </p>
                    </div>
                )}
                {backend.length > 0 && (
                    <div className="backend">
                        <h4>Backend</h4>
                        <p>
                            {backend.map((tech, index) => <small key={index}>{tech}</small>)}
                        </p>
                    </div>
                )}
            </div>
        </div>
    );
}
 
export default Project;