import React, { useState, useEffect, useRef } from 'react';

import Timestamps from './Timestamps';
import './Video.css';

const VideoPlayer = (props) => {
    const [currentTime, setCurrentTime] = useState('');
    const [durationTime, setDurationTime] = useState('');
    const [videoTime, setVideoTime] = useState(0)
    const [isMenu, setIsMenu] = useState(false);
    const [isFullScreen, setIsFullScreen] = useState(false);

    const video = useRef(null);
    const btn = useRef(null);
    const player = useRef(null);
    const src = useRef(null);
    const playPauseButton = useRef(null);
    const seekslider = useRef(null);
    const size = useRef(null);

    useEffect(() => {
        const videoCurrentTime = video.current.currentTime;
        const videoDuration = video.current.duration;

        const nt = videoCurrentTime * (100 / videoDuration);
        seekslider.current.value = nt; 

        let curmins = Math.floor(videoCurrentTime / 60);
        let cursecs = Math.floor(videoCurrentTime - curmins * 60);
        let durmins = Math.floor(videoDuration / 60);
        let dursecs = Math.floor(videoDuration - durmins * 60);

        cursecs = cursecs < 10 ? `0${cursecs}` : cursecs;
        dursecs = dursecs < 10 ? `0${dursecs}` : dursecs;
        curmins = curmins < 10 ? `0${curmins}` : curmins;
        durmins = durmins < 10 ? `0${durmins}` : durmins;

        setCurrentTime(curmins + ":" + cursecs);
        setDurationTime(durmins + ":" + dursecs);
        seekslider.current.value = 0;
    }, [isMenu])

    const videoSeek = () => {
        const seekTo = video.current.duration * (seekslider.current.value / 100);
        video.current.currentTime = seekTo;
    }

    const seekTimeUpdate = () => {
        const videoCurrentTime = video.current.currentTime;
        const videoDuration = video.current.duration;

        const nt = videoCurrentTime * (100 / videoDuration);
        seekslider.current.value = nt; 

        let curmins = Math.floor(videoCurrentTime / 60);
        let cursecs = Math.floor(videoCurrentTime - curmins * 60);
        let durmins = Math.floor(videoDuration / 60);
        let dursecs = Math.floor(videoDuration - durmins * 60);

        cursecs = cursecs < 10 ? `0${cursecs}` : cursecs;
        dursecs = dursecs < 10 ? `0${dursecs}` : dursecs;
        curmins = curmins < 10 ? `0${curmins}` : curmins;
        durmins = durmins < 10 ? `0${durmins}` : durmins;

        setCurrentTime(curmins + ":" + cursecs);
        setDurationTime(durmins + ":" + dursecs);
        setVideoTime(videoCurrentTime);

        if (video.current.ended || videoCurrentTime >= videoDuration){
            video.current.classList.add("inactive");
            
            playPauseButton.current.classList.add("fa-play");
            playPauseButton.current.classList.remove("fa-pause");

            btn.current.classList.remove("fade-out");
            btn.current.classList.remove("fa-pause");
            btn.current.classList.remove("fa-play");
            btn.current.classList.add("fa-redo-alt");
        }
    }
    
    const playPause = () => {
        if (video.current.paused){
            play();
        } else {
            pause();
        }

        btn.current.classList.toggle("fade-out");
        btn.current.classList.remove("fa-redo-alt");
        video.current.classList.toggle("inactive");
    }

    const play = () => {
        video.current.play()
        btn.current.classList.remove("fa-play");
        btn.current.classList.add("fa-pause");
        btn.current.classList.remove("fa-redo-alt");

        playPauseButton.current.classList.remove("fa-play");
        playPauseButton.current.classList.add("fa-pause");
    }

    const pause = () => {
        video.current.pause()
        btn.current.classList.remove("fa-play");
        btn.current.classList.add("fa-pause");

        playPauseButton.current.classList.add("fa-play");
        playPauseButton.current.classList.remove("fa-pause");
    }

    const handleClik = (e) => {
        setIsMenu(true)
        playPause()
    }

    const handleTimestampClick = (time) => {
        video.current.currentTime = time;
        btn.current.classList.remove("fa-pause");
        btn.current.classList.remove("fa-redo-alt");
        btn.current.classList.add("fa-play");
    }

    const toggleFullScreen = () => {
        if (isFullScreen) {
            closeFullScreen();
        } else {
            openFullScreen();
        }
        setIsFullScreen(!isFullScreen);
        size.current.classList.toggle('fa-expand');
        size.current.classList.toggle('fa-compress');
    }

    const openFullScreen = () => {
        const videoPlayer = player.current;
        if (videoPlayer.requestFullscreen) {
            videoPlayer.requestFullscreen();
        } else if (videoPlayer.mozRequestFullScreen) { /* Firefox */
            videoPlayer.mozRequestFullScreen();
        } else if (videoPlayer.webkitRequestFullscreen) { /* Chrome, Safari and Opera */
            videoPlayer.webkitRequestFullscreen();
        } else if (videoPlayer.msRequestFullscreen) { /* IE/Edge */
            videoPlayer.msRequestFullscreen();
        }
    }

    const closeFullScreen = () => {
        if (document.fullscreenElement) {
            if (document.exitFullscreen) {
                document.exitFullscreen();
            } else if (document.mozCancelFullScreen) { /* Firefox */
                document.mozCancelFullScreen();
            } else if (document.webkitExitFullscreen) { /* Chrome, Safari and Opera */
                document.webkitExitFullscreen();
            } else if (document.msExitFullscreen) { /* IE/Edge */
                document.msExitFullscreen();
            }
        }else{
            size.current.classList.toggle('fa-expand');
            size.current.classList.toggle('fa-compress');
        }
    }

    const handleKeyDown = (e) => {
        console.log(e)
    }

    const menuStyle = isMenu ? {visibility: 'visible'} : {visibility: 'hidden'};
    const videoStyle = isFullScreen ? {
        positon: 'absolute',
        top: 0,
        left: 0

    } : {};
    
    return (    
        <div className="player" ref={player} style={videoStyle}>
            <video ref={video} className="inactive" onTimeUpdate={seekTimeUpdate} muted onKeyDown={handleKeyDown}>
                <source type="video/mp4" src={props.video} ref={src} />
            </video>
            <div className="start-pause" onClick={handleClik}>
                <div className="control">
                    <i className="fas fa-play" ref={btn}></i>
                </div>
            </div>
            <div className="menu" style={menuStyle}>
                <div className="item play-pause">
                    <i className="fas fa-play" ref={playPauseButton} onClick={handleClik}></i>
                </div>
                <div className="item seekslider">
                    <input type="range" className="seekslider" min="0" max="100" step="1" ref={seekslider} onChange={videoSeek} />
                </div>
                <div className="item time">
                    <span className="currentTime"><small>{currentTime}</small></span><small>/</small><span className="durationTime"><small>{durationTime}</small></span>
                </div>
                <div className="item expand">
                    <i className="fas fa-expand" ref={size} onClick={toggleFullScreen}></i>
                </div>
            </div>
            <Timestamps timestamps={props.timestamps} videoCurrentTime={videoTime} style={menuStyle} timestampClick={handleTimestampClick}/>
        </div> 
    );
}
 
export default VideoPlayer;