import React from 'react';
import Timestamp from './Timestamp';

import './Timestamps.css'

const Timestamps = ({timestamps, videoCurrentTime, style, timestampClick}) => {
    return ( 
        <div className="timestamps" style={style}>
            {timestamps.map(timestamp => {
                return (
                    <Timestamp 
                        key={timestamp.id} 
                        videoCurrentTime={videoCurrentTime} 
                        timestampClick={timestampClick}
                        {...timestamp} 
                    />
                )
            })}
        </div> 
    );
}
 
export default Timestamps;