import React, { useState, useRef, Fragment } from 'react';

import './References.css';

const References = () => {
    const email = useRef(null);
    const [msgText, setMsgText] = useState('Skopiuj email')

    const copyEmail = () => {
        const mail = email.current;
        mail.style.display = 'block';

        /* Select the text field */
        mail.select();
        mail.setSelectionRange(0, 99999); /*For mobile devices*/
      
        /* Copy the text inside the text field */
        document.execCommand("copy");
        mail.style.display = 'none';

        setMsgText('Skopiowano email do schowka')
    }

    
    const handleMouseOut = () => {
        setMsgText('Skopiuj email')
    }


    return (
        <Fragment>
            <div className="references">
                <ul>
                    <li className="linkedin">
                        <a href="https://www.linkedin.com/in/marcin-góralczyk-39a164192/">
                            <i className="fab fa-linkedin-in"></i>
                        </a>
                        <div></div>
                    </li>
                    <li className="github">
                        <a href="https://github.com/emerengg">
                            <i className="fab fa-github"></i>
                        </a>
                        <div></div>
                    </li>
                    <li className="email">
                        <div className="tooltip">
                            <i className="fas fa-envelope"  onClick={copyEmail} onMouseOut={handleMouseOut}></i>
                            <input type="text" ref={email} value="mgoralczyk.pl@gmail.com" readOnly></input>
                            <span className="tooltiptext" id="myTooltip">{msgText}</span>
                        </div>
                    </li>
                </ul>
            </div>
        </Fragment>   
    )
}

export default References;