import React, { useState, useEffect, useRef } from 'react';

import './Timestamp.css';

const Timestamp = ({id, start_time, end_time, info, videoCurrentTime, timestampClick}) => {
    const [isActive, setIsActive] = useState(false)

    const timestamp = useRef(null);
    const span = useRef(null)
    const timer = useRef(null)

    useEffect(() => {
        const currentTime = Math.floor(videoCurrentTime);
        if (currentTime >= start_time && currentTime < end_time) {
            setIsActive(true)
        } else {
            setIsActive(false)
        }
    }, [videoCurrentTime, start_time, end_time])

    const handleClick = () => {
        timestampClick(start_time);
    }

    const style = isActive ? (
        {opacity: '1', backgroundColor: 'var(--primary-color)'}
    ) : (
        {opacity: '0.8'}
    );

    return ( 
        <div 
            className="timestamp"
            style={style} 
            ref={timestamp} 
            onClick={handleClick} 
        >
                <span ref={span}>{info}</span>
                <div className="timer" ref={timer}></div>
        </div>
    );
}
 
export default Timestamp;